/* Container
---------------------------------------------------------------------- */

.container {
    line-height: 0;
	margin-top: 45px;
    text-align: justify;
	
	@media (min-width: 700px) and (max-width: 992px) {
		margin-left: auto;
		margin-right: auto;
		max-width: 760px;
	}
}

.container:after {
    content: '';
    display: inline-block;
    width: 100%;
}

/* Target Elements
---------------------------------------------------------------------- */

.mix {
	margin-bottom: 40px;
	position: relative;
	
	.mix-inner {
		border: 1px solid #ddd;
		margin: 0 auto;
		max-width: 316px;
		
		position: relative;
		width: 100%;
		box-shadow: 2px 2px 2px 0px rgba(69,52,52,.2);
		-webkit-box-shadow: 2px 2px 2px 0px rgba(69,52,52,.2);
		-moz-box-shadow:2px 2px 2px 0px rgba(69,52,52,.2);
		
		> a {
			background: #000;
			display: block;
		}
		
		.btn {
			opacity: 0;
			left: 0;
			margin: 0 auto;
			right: 0;
			transition: all .2s ease-in-out;
			top: 30%;
			width: 140px;
			position: absolute;
			z-index: 5;
			
			a {
				padding: 1em;
			}
		}
		
		img {
			opacity: 1;
			transition: all .2s ease-in-out;
		}
		
		&:hover {
			img {
				opacity: 0.6;
			}
			
			.btn {
				opacity: 1;
			}
		}
	}
	
	
	.info {
		padding: 10px 5px;
		text-align: center;
		
		.info-inner {
			line-height: 1.15;
			
			h3 {
				
				@media (min-width: 768px) {
					font-size: 22px;
				}
			}
		}
	}
}
		


